


.cab-driver-home {
    display: contents;

    .cab-content-area {
        margin-left: 220px;
        flex-grow: 1;
        height: calc(100vh - 80px); 
        overflow-y: hidden;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 0px;
        position: relative;

        .cab-location-container {
            width: 100%;
            background-color: #f0f0f0;
            display: flex;
            align-items: center;
            justify-content: center;
            box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
            padding: 5px;
            z-index: 1;

            .cab-input {
                margin-right: 10px;
            }

            .cab-action-button {
                margin-left: 5px;
                padding: 4px 16px;
                background-color: #17a528d0;
                color: white;
                border: none;
                border-radius: 4px;
                cursor: pointer;

                &:hover {
                    background-color: #17a528;
                }
            }
        }

        .cab-content-container {
            width: 100%;
            height: 100%; // Fixed height for content area
            margin-top: auto; // Positioned below the location container
            overflow-y: auto; // Enable scrolling for content overflow
            padding: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .error {
                padding: 20px;
                color: red;
                background-color: white;
                font-size: 1.2em;
                text-align: center;
                max-width: 80%;
                border: 1px solid #f44336;
                border-radius: 5px;
                box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.1);
            }
        }

        .cab-child-link {
            width: 100%;
            text-align: center;
            margin-top: 20px;
        }
    }
}
