


$primary-color: #2C3E50; 
$secondary-color: #2980B9;
$light-gray: #ECF0F1; 
$white: #FFFFFF;
$dark-text: #34495E;
$red: #E74C3C;

.company-signup-page {
  margin-top: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 80px);
  background-color:  #bde0d6;

  .company-signup-content {
    background-color: rgba($white, 0.85); 
    border-radius: 8px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
    padding: 40px;
    width: 100%;
    max-width: 500px;
    text-align: center;
    overflow: hidden;
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .company-signup-form-container {
    max-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .company-welcome-container {
      margin-bottom: 20px;

      .company-title {
        font-size: 2em;
        color: $primary-color; 
        margin-bottom: 10px;
      }

      .company-message {
        color: darken($primary-color, 30%); 
        font-size: 1.1em;
      }
    }

    .company-signup-form {
      display: flex;
      flex-direction: column;
      overflow-y: auto;
      max-height: 400px;
      margin-top: 20px;

      .company-input-group {
        margin-bottom: 16px;
        text-align: left;

        label {
          font-weight: 600;
          color: $primary-color; 
          display: block;
          margin-bottom: 5px;
        }

        .company-input,
        .company-textarea {
          width: 100%;
          padding: 10px;
          border: 1px solid $primary-color; 
          border-radius: 4px;
          font-size: 0.9em;
          color: $dark-text; 
          box-sizing: border-box;
          margin-bottom: 8px;

          &:focus {
            border-color: $secondary-color; 
            outline: none;
          }
        }

        .company-textarea {
          height: 120px;
          resize: vertical;
        }
      }

      .company-btn-generic {
        padding: 12px;
        font-size: 1.1em;
        font-weight: 600;
        color: $white; 
        background-color: $primary-color; 
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s;

        &:hover {
          background-color: lighten($primary-color, 10%);
        }
      }
    }

    .company-login-link {
      margin-top: 20px;

      a {
        color: $secondary-color;
        font-weight: 600;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }
      }
    }

    .error-message {
      color: $red; 
      border-radius: 5px;
      // margin-top: 5px;
      padding-left: 5px;
      font-size: 12px;
      text-align: left;
    }
  }
}
