



.cab-data-table-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  overflow: hidden;

  .cab-data-table {
    width: 100%;
    border-collapse: collapse;
    table-layout: fixed;

    .cab-data-table-header-cell,
    .cab-data-table-cell {
      padding: 8px 12px;
      text-align: center; // Center-align table content
      vertical-align: middle;
      border-bottom: 1px solid #ddd;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      height: 40px;
      box-sizing: border-box;

      &.cab-data-table-header-cell {
        background-color: #f4f4f4;
        font-weight: bold;
        position: sticky;
        top: 0;
      }
    }

    .cab-data-table-action-cell {
      border-bottom: 1px solid #ddd;
      padding: 8px 12px;
      text-align: center;
      vertical-align: middle;
    }
  }

  .cab-data-table-body-container {
    width: 100%;
    overflow-y: auto;
    max-height: 400px;
    display: block;
  }

  .cab-action-button {
    padding: 6px 12px;
    color: #fff;
    background-color: #d9534f;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.2s;

    &.primary{
      background: #17a528d0;

      &:hover {
        background-color:#069617ec;
      }
  }

    &.secondary {
      background-color: #d9534f;

      &:hover {
        background-color: #c9302c;
      }
    }
  }
}
