


// Variables
$label-width: 40%;
$input-width: 55%;
$form-width: 40%;
$primary-color: #007bff;
$hover-color: #069617ec;
$error-color: red;
$font-color: #333;
$input-bg: #f9f9f9;
$border-radius: 4px;


.cab-profile-page{
    display: contents;
    .cab-content-area {
        margin-top: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
      }
}

// Main container styling
.cab-profile-form {
  width: $form-width;
  padding: 15px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin: 0 auto; // Centering the form within its container
  background-color:white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: $border-radius;
  padding-top: 20px;
  padding-bottom:20px ;

  // Error message styling
  .error {
    color: $error-color;
    font-weight: bold;
    margin-bottom: 12px;
    text-align: center;
  }

  // Form group layout: labels on the left, values on the right
  .cab-form-group {
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 8px;
    padding-left: 10px;

    label {
      width: $label-width;
      font-weight: bold;
      color: #333333;
      text-align: left;
      margin-right: 10px;
      font-size: 20px;
    }

    input[type="text"] {
      width: $input-width;
      padding: 6px;
      border: 1px solid #ccc;
      border-radius: $border-radius;
      background-color: $input-bg;
      font-size: 20px;
    }
  }

  .cab-action-button {
    padding: 8px 16px;
    border: none;
    border-radius: $border-radius;
    cursor: pointer;
    color: #fff;
    margin: 15px auto 0; 
    display: block; 

    &.primary {
      background-color: #17a528d0;
      &:hover {
        background-color:#069617ec ;
      }
    }

    &.secondary {
      background-color: #d9534f;
      margin-top: 5px;
      &:hover {
        background-color: #c9302c;
      }
    }
  }
}
