



@import 'colors';
$sidebar-link-color: white;
$sidebar-link-hover-bg: rgba(255, 69, 0, 0.2);
$sidebar-link-hover-color: #007bff;
$sidebar-link-active-bg: rgba(255, 165, 0, 0.2);
.cab-sidebar {
  .cab-sidebar__left-menu {
    .cab-sidebar__menu-item,
    .cab-sidebar__logout-btn {
      .cab-sidebar__link {
        cursor: pointer;
        text-align: left;
        display: block;
        padding: 15px;
        border-bottom: 1px solid $cab-sidebar-border-light;
        border-top: 1px solid $cab-sidebar-border-dark;
        transition: color 0.3s ease, background-color 0.3s ease;
        color: #FFFFFF;
        text-decoration: none;
        // margin-left: 5px;
        
        i {
          margin-right: 4px; // Ensure logout button icons are consistent
        }
        &.active-route{
          font-weight: bold;
          background-color: $cab-sidebar-link-active-background-color;
          color: $cab-sidebar-link-active-color;

          a {
            color: inherit;
          }
        }

        &:hover {
          font-weight: bold;
          background-color: $cab-sidebar-link-hover-background-color;
          color: $cab-sidebar-link-hover-color;
          a {
            color: inherit;
          }
        }
      }
    }

    .cab-sidebar__logout-btn {
      .cab-sidebar__link {
        background: none; 
        color: #d9534f; 
        font-weight: bold;
  
        &:hover {
          background: #d9534f; 
          // color: darkred; 
          color: white;
        }
      }
    }
  }
}