.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  z-index: 9;
}

.modal {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #ffffff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 10;
  max-width: 400px;

  p {
    font-size: 1em;
    color: #333;
    margin-bottom: 20px;
  }

  .button-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }

  button {
    padding: 10px 15px;
    margin: 5px;
    font-size: 0.9em;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:first-of-type {
      background-color: #17a528d0;
      color: #fff;
      margin-right: 5px;  // Adds space between buttons

      &:hover {
        background-color: #069617ec;
      }
    }

    &:last-of-type {
      background-color: #d9534f;
      color: #fff;

      &:hover {
        background-color: #c9302c;
      }
    }
  }
}

.alert-message {
  position: fixed;
  top: 82px;
  left: 50%;
  transform: translateX(-50%);
  font-weight: bold;
  color: #e63946;
  padding: 10px 20px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 11;
  text-align: center;
  max-width: 500px;  // Limit the max width for better appearance on smaller screens
}
