.cab-ride-details-table {
  max-width: 600px;
  margin: auto;
  padding: 20px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

  table {
    width: 100%;
    border-collapse: collapse;

    td {
      padding: 12px;
      vertical-align: middle;
      border-bottom: 1px solid #ddd;
      white-space: nowrap; 

      &:first-child {
        font-weight: bold;
        color: #333;
        text-align: left;
        padding-right: 15px;
        width: 35%;
      }

      &:last-child {
        width: 65%;
        text-align: left;
      }
    }

    .otp-row {
      

      .error-message {
        font-size: 0.9em;
        color: #e63946;
        flex-shrink: 0;
      }

      #otp-input {
        flex-grow: 1;
        width: 100%;
        padding: 8px;
        border: 1px solid #ddd;
        border-radius: 4px;
        box-sizing: border-box;
      }
    }

    .action-cell {
      text-align: center;
      padding-top: 15px;

      button {
        padding: 8px 16px;
        background-color: #d9534f;
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s;

        &:hover {
          background-color: #c9302c;
        }

        &:focus {
          outline: none;
          box-shadow: 0 0 0 3px rgba(217, 83, 79, 0.3);
        }

        &:disabled {
          background-color: #ddd;
          cursor: not-allowed;
        }
      }
      .cab-action-button.primary {
        background-color: #28a745; 
        color: white; 
        &:hover {
          background-color: #218838; 
        }
      
        &:focus {
          box-shadow: 0 0 0 1px rgba(40, 167, 69, 0.3); 
        }
      
        &:disabled {
          background-color: #ddd; 
          color: #6c757d; 
        }
      }
      
    }
  }
}
