
.cab-user-home {
  display: contents;

  .cab-content-area {
    margin-top: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
 
    
  .cab-book-page {
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
    text-align: center;
    width: 400px;
    transition: transform 0.3s ease-in-out;
    margin-bottom: 100px;
    background-color: white;
    &:hover {
      transform: scale(1.02);
    }
  }

  .slogan {
    font-size: 1.5em;
    color: #069617ec;
    margin-bottom: 15px;
    font-weight: 600;
  }

  .cab-form-group {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .cab-form-item {
    margin-bottom: 15px;
    text-align: left;
  }

  .cab-label {
    display: block;
    font-size: 1em;
    color: #333333;
    margin-bottom: 6px;
  }

  .cab-select,
  .cab-input {
    width: 100%;
    padding: 10px 12px;
    border: 1px solid #ddd;
    border-radius: 6px;
    font-size: 0.9em;
    color: #555555;
    background-color: #fafafa;
    transition: border-color 0.3s ease;

    &:focus {
      border-color: #069617ec;
      outline: none;
    }
  }

  .cab-submit-btn {
    width: 100%;
    padding: 10px;
    font-size: 1em;
    font-weight: 600;
    color: #ffffff;
    background-color: #17a528d0;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #069617ec;
    }
  }

  .error-message {
    color: #f44336;
    border-radius: 5px;
    margin-top: 5px;
    padding-left: 5px;
    font-size: 12px;
  }

  .cab-back-btn {
    position: absolute;
    top: 10px;
    left: 10px;
    background-color: #17a528d0;
    // background-color: #007bff;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    padding: 8px 12px;
    font-size: 0.9em;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #069617ec;
    }
  }
}
