.not-found-message {
    display: flex;
    flex-direction: column;  
    justify-content: center;
    align-items: center;
    height: 100vh;
    text-align: center;
    background-color: #f2f2f2; 
    h1 {
      font-size: 2.5rem;
      color: #ff0000;  
      margin: 0;  
    }
  
    p {
      font-size: 1.2rem;
      color: #555; 
      margin-top: 20px;  
      max-width: 500px;  
      line-height: 1.6;  
      padding: 0 15px;  
    }
  }
  