.cab-dashboard-container {
    display: flex;
    flex-direction: column;
    height: 100vh;
  
    .navbar {
      height: 80px;
      background-color: #111;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 20px;
      z-index: 10; 
    }
  
    .content {
      display: flex;
      flex: 1;
      height: calc(100vh - 80px); 
    //   flex-wrap: wrap;
    //   flex-direction: column;
    }
  
    .left, .right {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 100%;
    }
  
    .left {
        // background-image:url("background6.jpeg");
      img {
        width: 100%;
        max-width: 100%;
      }
    }
  
    .right {
      padding: 20px;
      background-color: #f4f4f4;
    }
  }
  