.cab-history-page {
  display: contents;

  .cab-content-area {
      margin-top: 0px;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
  }

  .overlay {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.5);
      z-index: 10; 
  }


  .report-modal {
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 400px;
      max-width: 90%;
      padding: 20px;
      background-color: #fff;
      border-radius: 8px;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
      z-index: 11; 

      h3 {
          text-align: center;
          margin-bottom: 16px;
      }

      label {
          display: block;
          margin-bottom: 8px;
          font-weight: bold;
      }

      input[type="text"] {
          width: 100%;
          padding: 10px;
          border: 1px solid #ddd;
          border-radius: 4px;
          margin-bottom: 16px;
      }

      .modal-actions {
          display: flex;
          justify-content: space-between;

          button {
              padding: 8px 16px;
              border: none;
              border-radius: 4px;
              cursor: pointer;
              font-weight: bold;
              transition: background-color 0.3s ease;

              &:first-child {
                  background-color: #17a528d0;
                  color: #fff;

                  &:hover {
                      background-color: #069617ec;
                  }
              }

              &:last-child {
                  background-color: #dc3545;
                  color: #fff;

                  &:hover {
                      background-color: #c82333;
                  }
              }
          }
      }
  }
}
