*{
  padding: 0;
  margin:0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}


body,input {
  font-family: 'Times New Roman', Times, serif;
}

.main-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; 
}

.cab-content-area {
  background-color: #bde0d6;
}


.taxi-container {
  display: flex;
  flex-direction: column; 
  height: 100%; 
}


.cab-nav-bar {
  position: fixed; 
  top: 0;
  left: 0;
  right: 0;
  height: 80px; 
  background-color: #f8f9fa; 
  z-index: 1; 
}

.cab-display-area {
  background-color: #f8f9fc; 
  display: flex;
  margin-top: 80px;
  height: calc(100vh - 80px); 
}

.cab-side-bar {
    width: 220px; 
    height: calc(100vh - 80px); 
    background-color: #2D3E50; 
    position: fixed; 
    top: 80px;
    left: 0;
    display: flex;
    flex-direction: column;
    padding: 20px;
    color: white;
    text-align: center;
    color: white;
    z-index: 1;
}




.cab-content-area {

  flex-grow: 1; 
  margin-left: 220px; 
  padding: 20px;
  display: flex;
  flex-direction: column; 
  align-items: center; 
  justify-content: center; 
  height: calc(100vh - 80px); 
  overflow-y: auto; 

  .cab-error-message {
    color: red;
    font-size: 18px; 
    margin-bottom: 20px; 
    text-align: center; 
    background-color: white;
  }

  .cab-booking-form {
    width: 100%; 
    max-width: 600px; 
    margin: 0 auto; 
  }
}



.cab-results-container {
  padding: 10px;
  border-radius: 8px;
}

.cab-results-header {
  display: flex; 
  align-items: center; 
  padding-bottom: 5px; 
}

.cab-back-btn {
  padding: 8px 16px;
  background-color: #00ff44;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;

  &:hover {
    background-color: #00b368;
  }
}

.cab-results-table {
  margin-top: 5px; 
}

.cab-error-message {
  background-color: #f8d7da; 
  color: red;           
  border: 1px solid #ede7e7; 
  border-radius: 5px;       
  padding: 10px 15px;      
  font-size: 14px;          
  font-weight: bold;        
  font-family: Arial, sans-serif; 
  margin: 10px 0;          
  display: inline-block;    
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); 
}

.cab-results-table {
  .cab-data-table-wrapper {
      .cab-data-table-container {
          .cab-data-table {
              tbody {
                  .cab-data-table-row {
                      .cab-data-table-action-cell {
                          .cab-action-button {
                            &.secondary {
                              background-color: #17a528d0;
                              &:hover {
                                background-color:#069617ec ;
                              }
                            }
                          }
                      }
                  }
              }
          }
      }
  }
}


