


$primary-color: #2C3E50; 
$secondary-color: #2980B9;
$light-gray: #ECF0F1; 
$white: #FFFFFF;
$dark-text: #34495E;
$red: #E74C3C;

.cab-signup-page {
  margin-top: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 80px);
  background-color: #bde0d6; 

  .cab-signup-content {
    background-color: rgba($white, 0.95); // Slight transparency
    border-radius: 8px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
    padding: 30px;
    width: 100%;
    max-width: 450px;
    text-align: center;
  }

  .cab-signup-form-container {
    .cab-welcome-container {
      margin-bottom: 20px;

      .cab-title {
        font-size: 1.8em;
        color: $primary-color;
        margin-bottom: 10px;
      }

      .cab-message {
        color: $dark-text;
        font-size: 1em;
      }
    }

    .cab-signup-form {
      display: flex;
      flex-direction: column;

      .cab-input-group {
        margin-bottom: 15px;

        label {
          font-weight: 600;
          color: $dark-text;
          display: block;
          margin-bottom: 5px;
          text-align: left;
        }

        .cab-input {
          padding: 10px;
          border: 1px solid $light-gray;
          border-radius: 4px;
          font-size: 0.9em;
          color: $dark-text;
          background-color: $white;
          width: 100%;
          border: 1px solid #2C3E50;
          &:focus {
            border-color: $secondary-color;
            outline: none;
          }
        }
      }

      .cab-btn-generic {
        padding: 10px;
        font-size: 1em;
        font-weight: 600;
        color: $white;
        background-color: $primary-color;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s;

        &:hover {
          background-color: lighten($primary-color, 10%);
        }
      }
    }

    .cab-login-link {
      margin-top: 20px;

      a {
        color: $secondary-color;
        font-weight: 600;
        text-decoration: none;

        &:hover {
          text-decoration: underline;
        }
      }
    }

    .error-message {
      color: $red;
      border-radius: 5px;
      // margin-top: 5px;
      padding-left: 5px;
      font-size: 12px;
      text-align: left;
    }
  }
}
