.cab-content-area {
  background-color: #f8f9fc;
  flex-grow: 1;
  margin-left: 200px; 
  padding: 20px;
  margin-top: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 80px); 
  overflow-y: auto;

  .form-container {
    width: 100%;
    max-width: 400px;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    .form-container-title {
      font-size: 1.5em; 
      font-weight: bold;
      color: #17a528d0; 
      margin-bottom: 20px; 
      text-align: center; 
    }

    form {
      .form-group {
        margin-bottom: 15px;

        label {
          display: block;
          margin-bottom: 5px;
          font-weight: 600;
          color: #333;
        }

        .form-control {
          width: 100%;
          padding: 10px;
          border: 1px solid #ced4da;
          border-radius: 4px;
          font-size: 0.9em;
          color: #555;
          background-color: #fafafa;
          transition: border-color 0.3s ease;

          &:focus {
            border-color: #007bff;
            outline: none;
          }
        }
      }

      .btn {
        width: 100%;
        padding: 10px;
        margin-top: 10px;
        font-size: 1em;
        font-weight: 600;
        color: #ffffff;
        border: none;
        border-radius: 6px;
        cursor: pointer;
        transition: background-color 0.3s ease;

        &.btn-primary {
          background-color: #17a528d0;
          &:hover {
            background-color:#069617ec ;
          }
        }

        &.btn-secondary {
          background-color: #d9534f;
          margin-top: 5px;
          &:hover {
            background-color: #c9302c;
          }
        }
      }
    }
  }
  .error-message {
    color: #f44336;
    border-radius: 5px;
    padding-left: 5px;
    font-size: 12px;
  }
}

