

.cab-data-table-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: white;
  
    .cab-data-table-container {
      width: 100%;
      max-width: 1600px;
      display: flex;
      flex-direction: column; 
      border-radius: 8px;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      overflow: hidden;
      padding: 10px;
  
      .cab-data-table {
        width: 100%;
        border-collapse: collapse;
        text-align: center;
  
        thead {
          .cab-data-table-header {
            background-color: #007bff;
            color: #432424;
            display: table;
            width: 100%;
            table-layout: fixed;
  
            .cab-data-table-header-cell {
              padding: 12px;
              font-weight: bold;
              text-align: center;
            }
          }
        }
  
        tbody {
          display: block;
          max-height: 400px;
          overflow-y: auto;
          width: 100%;
  
          .cab-data-table-row {
            display: table;
            width: 100%;
            table-layout: fixed;
  
            &:nth-child(even) {
              background-color: #f9f9f9;
            }
  
            .cab-data-table-cell,
            .reported-status {
              padding: 1px;
              text-align: center;
            }
  
            .cab-data-table-action-cell {
              display: flex;
              justify-content: center;
              align-items: center;
              gap: 10px;
              // flex-wrap: wrap; 
  
              .cab-action-button {
                padding: 6px 10px;
                font-size: 1em;
                flex-shrink: 1;
                max-width: 100px; 
                &.primary{
                  background: #17a528d0;

                  &:hover {
                    background-color:#069617ec;
                  }
              }
  
                &.secondary {
                  background-color: #d9534f;
  
                  &:hover {
                    background-color: #c9302c;
                  }
                }
              }
            }
          }
        }
  
        tfoot {
          display: table;
          width: 100%;
          table-layout: fixed;
          background-color: #f1f1f1;
  
          .cab-data-table-footer-cell {
            padding: 12px;
            font-weight: bold;
            text-align: center;
          }
        }
      }
  
      .reported-status {
        color: #6c757d;;
        font-weight: bold;
        font-size: 15px;
      }
    }
  }