

$primary-color: #2C3E50; 
$secondary-color: #2980B9; 
$light-gray: #ECF0F1; 
$white: #FFFFFF;
$dark-text: #34495E;
$red: #E74C3C; 

.cab-login-page {
  margin-top: 80px; // Offset for the fixed auth-links
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 80px); // Full height minus the header
  background-color:  #bde0d6;
}

// Centered Login Content with transparent background
.cab-login-content {
  background-color: rgba($white, 0.85); // Slight transparency for a modern look
  border-radius: 8px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  padding: 40px;
  width: 100%;
  max-width: 400px;
  text-align: center;
}

// Login Form Container
.cab-login-form-container {
  .cab-welcome-container {
    margin-bottom: 20px;

    .cab-title {
      font-size: 1.8em;
      color: $primary-color; // Gold color for the title
      margin-bottom: 10px;
    }

    .cab-message {
      color: darken($primary-color, 30%); // Darkened gold for a subtle message
      font-size: 1em;
    }
  }

  .cab-login-form {
    display: flex;
    flex-direction: column;

    .cab-input-group {
      margin-bottom: 20px;
      text-align: left;

      label {
        font-weight: 600;
        color: $primary-color; // Gold for the labels
        display: block;
        margin-bottom: 5px;
      }

      .cab-input {
        width: 100%;
        padding: 10px;
        border: 1px solid $primary-color; // Gold border for inputs
        border-radius: 4px;
        font-size: 1em;
        color: $dark-text; // Dark text color for input
        box-sizing: border-box;

        &:focus {
          border-color: $secondary-color; // Change to orange accent on focus
          outline: none;
        }
      }
    }

    .cab-btn-generic {
      padding: 12px;
      font-size: 1em;
      font-weight: 600;
      color: $white; // White text on button
      background-color: $primary-color; // Gold background for button
      border: none;
      border-radius: 4px;
      cursor: pointer;
      transition: background-color 0.3s;

      &:hover {
        background-color: lighten($primary-color, 10%); // Slightly darker gold on hover
      }
    }
  }

  .cab-signup-link {
    margin-top: 20px;

    a {
      color: $secondary-color;
      font-weight: 600;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .error-message {
    color: $red; // Red color for error message
    border-radius: 5px;
    // margin-top: 5px;
    padding-left: 5px;
    font-size: 12px;
  }
}
