

@import 'colors';
.cab-navbar-container {
  position: fixed; 
  top: 0; 
  height: 80px; 
  width: 100%; 
  background-color: $cab-navbar-background-color; 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); 
  z-index: 1; 

  .cab-navbar { 
    height: 100%; 
    display: flex; 
    align-items: center; 
    justify-content: space-between; 
    padding: 0 2%; 
  }

  .cab-navbar-logo {
    display: flex; 
    align-items: center; 
    font-size: 40px;
    font-weight: bold;
    color: #2980B9; 
    cursor: pointer;
    text-decoration: none;
    transition: color 0.4s ease-in-out, font-weight 0.3s ease;
    img {
      width: 50px; 
      height: 50px; 
      border-radius: 50%; 
      margin-right: 10px; 
      border: 2px solid #2980B9; 
      object-fit: cover; 
    }
  }

  .cab-navbar_welcome {
    font-size: 25px; 
    font-weight: bold;
    color: $cab-navbar-white-color;
    text-transform: uppercase; 
    margin-left: 20px; 
  }

  .cab-navbar-links {
    display: flex; 
    align-items: center; 
    gap: 20px; 

    .cab-navbar__link-item,
    .cab-navbar__switch-role,
    .cab-navbar__link {
      border-radius: 5px;
      color: $cab-navbar-white-color;
      cursor: pointer;

      &:hover{
        font-weight: bold;
        color: $cab-sidebar-link-hover-color;
        background-color: $cab-sidebar-link-hover-background-color;
      }

      &.active {
        padding: 2px 10px;
        color: $cab-sidebar-link-active-color;
        font-weight: bold;
        background-color: $cab-sidebar-link-active-background-color;
      }
    }
  }
}

