.cab-data-table-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    // background-color: #f8f9fc; // Light background for the wrapper

    .cab-data-table-container {
        width: 80%;
        max-width: 1000px;
        background-color: #ffffff;
        border-radius: 8px;
        box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
        overflow: hidden;
        font-family: Arial, sans-serif; // Sans-serif font style for the text

        .cab-data-table {
            width: 100%;
            border-collapse: collapse;

            thead {
                .cab-data-table-header {
                    background-color: #007bff; // Light blue for header background
                    color: #ffffff; // White text for header
                }
                
                .cab-data-table-header-cell {
                    padding: 10px;
                    text-align: left;
                    font-weight: bold;
                    font-size: 14px;
                }
            }

            tbody {
                display: block;
                max-height: 300px;
                overflow-y: auto;

                .cab-data-table-row {
                    display: table;
                    width: 100%;
                    table-layout: fixed;
                    font-size: 14px;
                    
                    &:nth-child(even) {
                        background-color: #f9f9f9; // Light gray for alternating row
                    }

                    &:nth-child(odd) {
                        background-color: #ffffff; // White for other rows
                    }
                    
                    .cab-data-table-cell, 
                    .cab-data-table-action-cell {
                        padding: 10px;
                        text-align: left;
                        border-bottom: 1px solid #ddd; // Light border between rows
                    }
                }
            }
        }

        .cab-action-button {
            padding: 6px 12px;
            margin-right: 5px;
            color: #ffffff;
            border: none;
            border-radius: 4px;
            cursor: pointer;
            transition: background-color 0.3s ease;
            &.primary{
                background: #17a528d0;

                &:hover {
                  background-color:#069617ec;
                }
            }

              &.secondary {
                background-color: #d9534f;

                &:hover {
                  background-color: #c9302c;
                }
              }
        }
        
    }
}
