.cab-result-container {
    position: relative;

    .cab-back-btn {
        position: absolute;
        top: 10px;
        left: 10px;
        padding: 8px 12px;
        background-color: #4CAF50; // Green
        color: white;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s;

        &:hover {
            background-color: #45a049; // Darker green
        }
    }
}
